.App {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  background-color: black;
  color: white;
}

.App-header {
  background-image: url(./imgs/home.jpg);
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.intro{
  height: 650px;
  background-color: rgba(80, 80, 80, 0.87);
  width: 100%;
  color: rgb(150, 150, 150);
  transition: 0.7s;
}
.intro:hover{
  background-color: rgb(0, 0, 0);
  color: rgb(255, 255, 255);
  transition: 0.7s;
}
#subtitle{
  width: 100%;
  height: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 1vmin);
  font-weight: 500;
}
#title{
  width: 100%;
  height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  font-size: calc(35px + 1vmin);
  font-weight: 500;
  padding-bottom: 15px;
}
.title-card{
  background-color: rgba(0, 0, 0, 0.7);
  width: 100%;
  height: 250px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

@media (max-width: 350px) {
  #subtitle{
  font-size: calc(10px + 1vmin);
    font-weight: 500;
  }
  #title{
    font-size: calc(25px + 1vmin);
    font-weight: 500;
    padding-bottom: 15px;
    text-align: center;
  }
}
@media (max-width: 450px) {
  #title{
    border: 2px solid red;
  }
}

@media (min-width: 700px) {
  #subtitle{
    font-size: 20px;
    font-weight: 500;
  }
  #title{
    font-size: 50px;
    font-weight: 500;
    padding-bottom: 15px;
  }
}
